import React from "react";
import { StaticQuery, graphql } from "gatsby";
import Form from "../common/forms/signup";
import Logo from "../../content/images/icon.png";

const gradient = "linear-gradient(to bottom, #3160ff 70%,#339ee0 95%,#f4f4f4";

const cardClasses = `
w-100 mw6 ma3 flex flex-column
 justify-center items-center pa2 br3
`;
const cardStyles = { width: "22rem", height: "22rem" };

const Card = (props) => (
  <div className={cardClasses} style={cardStyles}>
    {props.children}
  </div>
);

export default () => (
  <StaticQuery
    query={graphql`
      query {
        heroJson {
          title
          body
          description
        }
      }
    `}
    render={(data) => (
      <div
        className="min-vh-100 ph1 pv4 flex flex-column items-center justify-center"
        id="hero"
        style={{ backgroundImage: gradient }}
      >
        <Card>
          <img src={Logo} alt="" style={{ borderRadius: 10000 }} />
        </Card>
        <h1 className="near-white f1 mb2 tc">{data.heroJson.title}</h1>
        <span className="near-white f4 mw7 tc mb4">{data.heroJson.body}</span>
        <Form />
        <span className="near-white f4 mw7 tc mt4">
          {data.heroJson.description}
        </span>
      </div>
    )}
  />
);
